/* ============================================= */
/*              Base styles                      */
/* ============================================= */

* {
    font-family: $font-stack-primary;
    ;
    color: $color-secondary;
    box-sizing: border-box;
}

p {
    line-height: 1.45em;
}

a {
    text-decoration: none;
}


/* ============================================= */
/*              Typography                       */
/* ============================================= */

.headline {

    &-primary {
    color: $color-primary;
    margin-left: 10px;
}

    &-secondary {
        font-weight: $weight-secondary;
        color: $color-secondary;
    }
}





.link {
    color: $color-tertiary;
    font-weight: $weight-link;
}

.blockquote {
    font-size: 1.5em;
    font-weight: $weight-secondary;
    font-style: italic;
    margin-left: 20px;
    padding-left: 20px;
    border-left: 8px solid $color-primary;
    border-radius: 5px;
}
